@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
$break-1000: 1000px;
$break-mobile: 411px;

.allContactInfo {
  width: 55%;
  margin: 3em auto 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  @media screen and (max-width: $break-1000) {
    width: 100%;
    flex-direction: column;
  }
  .allAboutInfo {
    @media screen and (max-width: $break-1000) {
      margin: 0 auto 5em auto;
    }
  }
}

.bottomSection {
  width: 70%;
  margin: 0 0 0 10%;
  @media screen and (max-width: $break-1000) {
    width: 90%;
    margin: auto;
  }
  h1 {
    color: #98C263;
    font-size: 1.75em;
    margin: 0 0 .5em 0;
  }
}

.form-group {
  font-family: "Roboto", sans-serif;
  .message {
    height: 60%;
  }
}

.form-control {
  font-family: "Roboto", sans-serif;
  font-size: 1.65em;
}

#name {
  width: 100%;
  height: 1.75em;
  padding: 0 0 0 .5em;
  border: solid 1px gray;
  margin: 0 0 1em 0;
  @media screen and (max-width: $break-mobile) {
    width: 95%;
  }
}

#email {
  width: 100%;
  height: 1.75em;
  padding: 0 0 0 .5em;
  border: solid 1px gray;
  margin: 0 0 1em 0;
  @media screen and (max-width: $break-mobile) {
    width: 95%;
  }
}

#message {
  width: 100%;
  height: 200px;
  padding: .4em 0 0 .5em;
  border: solid 1px gray;
  margin: 0 0 1em 0;
  @media screen and (max-width: $break-mobile) {
    width: 95%;
  }
}

.btn {
  width: 101.5%;
  border: solid 1px gray;
  background-color: white;
  font-family: "Roboto", "sans-serif";
  color: #98C263;
  font-size: 2em;
  @media screen and (max-width: $break-mobile) {
    width: 99%;
  }
}
