@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.headerBlock{display:flex;flex-direction:row;justify-content:space-around;border-bottom:1px solid #e0e0e0;height:10em}@media screen and (max-width: 1000px){.headerBlock{max-width:1000px}}@media screen and (max-width: 800px){.headerBlock{display:flex;flex-direction:column}}.headerBlock :link{text-decoration:none}.headerBlock .titleBlock{width:auto;height:auto;margin:1.5em 0 0 0}@media screen and (max-width: 800px){.headerBlock .titleBlock{width:100%}}.headerBlock .name{font-size:4em;font-family:"Roboto", sans-serif;color:#efc14b}@media screen and (max-width: 800px){.headerBlock .name{width:100%;display:flex;justify-content:center}}.headerBlock .subtitle{font-size:1em;font-family:"Roboto", sans-serif;color:#7ba02d}@media screen and (max-width: 800px){.headerBlock .subtitle{width:100%;display:flex;justify-content:center}}.navBlock{width:auto;height:auto}@media screen and (max-width: 800px){.navBlock{display:flex;justify-content:center}}.navBlock .navbar{height:auto;width:20em;margin:4em 0 0 0;display:flex;justify-content:space-around}@media screen and (max-width: 800px){.navBlock .navbar{margin:2em 0 2em 0}}.navBlock .navbar :link{text-decoration:none;color:#4e4a4a}.navBlock .navbar :visited{color:#4d4a4a}.projectSection{margin:0 auto 0 auto}

.projects{width:80%;justify-content:center;justify-content:space-evenly;margin:0 auto 0 auto;display:flex;flex-direction:row;flex-wrap:wrap}@media screen and (max-width: 1000px){.projects{width:50%;margin:0 auto 0 auto}}@media screen and (max-width: 411px){.projects{margin:0;width:auto}}

@media screen and (max-width: 1000px){.boxes{display:flex;flex-direction:column;justify-content:center}}.boxes .projectBox{border:1px solid #919191;height:20em;width:30em;margin:3em 6em 2em 6em;background-color:#8d8c8c}@media screen and (max-width: 1000px){.boxes .projectBox{margin:3em 0 0 0}}@media screen and (max-width: 411px){.boxes .projectBox{width:99%;height:98%;margin:2em auto 1em auto}}.boxes .projectBox img{height:20em;width:30em}@media screen and (max-width: 1000px){.boxes .projectBox img{width:100%;height:100%}}.boxes .caption{height:1.5em;width:30em;margin:auto}@media screen and (max-width: 1000px){.boxes .caption{margin:1em auto 0 auto}}@media screen and (max-width: 411px){.boxes .caption{width:85%;margin:0 auto 2em auto}}

.footer{display:flex;justify-content:center;margin:5em 0 0 0;font-size:.75em;color:#403E3E;border-top:solid 1px #E0E0E0;left:0;bottom:0;width:100%}@media screen and (max-width: 1000px){.footer{max-width:1000px}}@media screen and (max-width: 411px){.footer{text-align:center}}.footer :link{text-decoration:none;color:#8d8c8c;font-weight:bold}.footer :link :visited{color:#8d8c8c}

ul{list-style-type:none;padding:0;color:#383838}.skillsTop{display:flex;flex-direction:column;justify-content:center;margin:2em 0 0 0}@media screen and (max-width: 1000px){.skillsTop{width:100%}}@media screen and (max-width: 411px){.skillsTop{margin:0 auto 0 auto;width:85%;align-items:center;text-align:center}}.skillsTop .skillsTopRow{display:flex;flex-direction:row;justify-content:center}@media screen and (max-width: 1000px){.skillsTop .skillsTopRow{display:flex;flex-direction:column;margin:auto}}@media screen and (max-width: 411px){.skillsTop .skillsTopRow{margin:0;align-items:center}}.skillsTop .skillsBottomRow{display:flex;flex-direction:row;justify-content:center}@media screen and (max-width: 1000px){.skillsTop .skillsBottomRow{display:flex;flex-direction:column;margin:auto}}@media screen and (max-width: 411px){.skillsTop .skillsBottomRow{margin:0}}hr{color:#383838}.skillsBottom{display:flex;justify-content:center;margin:3em 0 0 0}@media screen and (max-width: 411px){.skillsBottom{margin:0 auto 0 auto;width:85%;text-align:center}}

.skillSection{display:flex;flex-direction:row}@media screen and (max-width: 411px){.skillSection{width:95%;display:flex;flex-direction:column}}.skillSection .title{color:#7ba02d;font-family:"Roboto", sans-serif;font-weight:bold;font-size:1.2em;line-height:2em;width:11em;margin:.65em 3em 0 0}@media screen and (max-width: 411px){.skillSection .title{margin:1em 0 0 0;width:auto}}.skillSection .list{font-family:"Roboto", "san-serif";line-height:2em;width:14em;margin:0 3em 0 0}@media screen and (max-width: 411px){.skillSection .list{margin:0;width:auto}}

.allAboutInfo{display:flex;flex-direction:column;width:70%}.allAboutInfo .aboutSection{display:flex;flex-direction:column;justify-content:center;margin:2em 0 10em 0}.allAboutInfo .aboutSection img{width:70%;margin:0.75em auto 0 auto}.allAboutInfo p{width:70%;text-align:center;margin:1em auto 0 auto}.allAboutInfo p :link{text-decoration:none;color:#4d4a4a;font-weight:bold}.allAboutInfo p :link :visited{color:#4d4a4a}

.allContactInfo{width:55%;margin:3em auto 0 auto;display:flex;flex-direction:row;justify-content:space-evenly}@media screen and (max-width: 1000px){.allContactInfo{width:100%;flex-direction:column}}@media screen and (max-width: 1000px){.allContactInfo .allAboutInfo{margin:0 auto 5em auto}}.bottomSection{width:70%;margin:0 0 0 10%}@media screen and (max-width: 1000px){.bottomSection{width:90%;margin:auto}}.bottomSection h1{color:#98C263;font-size:1.75em;margin:0 0 .5em 0}.form-group{font-family:"Roboto", sans-serif}.form-group .message{height:60%}.form-control{font-family:"Roboto", sans-serif;font-size:1.65em}#name{width:100%;height:1.75em;padding:0 0 0 .5em;border:solid 1px gray;margin:0 0 1em 0}@media screen and (max-width: 411px){#name{width:95%}}#email{width:100%;height:1.75em;padding:0 0 0 .5em;border:solid 1px gray;margin:0 0 1em 0}@media screen and (max-width: 411px){#email{width:95%}}#message{width:100%;height:200px;padding:.4em 0 0 .5em;border:solid 1px gray;margin:0 0 1em 0}@media screen and (max-width: 411px){#message{width:95%}}.btn{width:101.5%;border:solid 1px gray;background-color:white;font-family:"Roboto", "sans-serif";color:#98C263;font-size:2em}@media screen and (max-width: 411px){.btn{width:99%}}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

